<template>
  <i class="my-icon" :class="classes" @click="handleClick">
    <component v-if="svg" :is="name" v-bind="svgProps"></component>
    <slot></slot>
  </i>
</template>

<script>

  /**
   * 
   * @module $ui/components/my-icon
   *
   */

  /**
   *
   * Theme 
   * @static
   * @enum Theme {String}
   */
  const Theme = {
    /**
     * 
     */
    DEFAULT: '',
    /**
     * 
     */
    SUCCESS: 'success',
    /**
     * 
     */
    WARNING: 'warning',
    /**
     * 
     */
    DANGER: 'danger',
    /**
     * 
     */
    INFO: 'info',
    /**
     * 
     */
    PRIMARY: 'primary'
  }

  /**
   * 
   * @member slots
   * @property {string} [default] svg
   */
  export default {
    Theme,
    name: 'MyIcon',
    /**
     * 
     * @member props
     * @property {string} [name] className  Element IconFont  svg
     * @property {Boolean} [svg] svg
     * @property {Object} [svgProps] svg{viewBox, width, height, fill, class}
     * @property {Theme} [theme] primarysuccesswarningdangerinfo
     */
    props: {
      name: String,
      svg: Boolean,
      svgProps: Object,
      theme: {
        type: String,
        validator(val) {
          return !val || Object.values(Theme).includes(val)
        }
      }
    },
    computed: {
      classes() {
        return [{
          [`my-icon--${this.theme}`]: !!this.theme,
          'is-pointer': !!this.$listeners.click,
          'is-svg': this.svg
        },
          // , name 
          this.$slots.default ? '' : this.name ? this.name : ''
        ]
      }
    },
    methods: {
      handleClick(e) {
        /**
         * 
         * @event click
         *
         */
        this.$emit('click', e)
      }
    }
  }
</script>

